import React from 'react';
import { ComponentStory, ComponentMeta } from '@storybook/react';
import { UserCard } from './UserCard';
import { ANONYMOUS_PRINCIPAL_ID, AVATAR, LARGE_USER_CARD, MEDIUM_USER_CARD, SMALL_USER_CARD } from '../utils/SynapseConstants';
import { useGetCurrentUserProfile } from '../utils/hooks/SynapseAPI/user/useUserBundle';
export default ({
  title: 'UI/UserCard',
  component: UserCard
} as ComponentMeta<typeof UserCard>);
const Template: ComponentStory<typeof UserCard> = args => {
  const {
    data: currentUserProfile
  } = useGetCurrentUserProfile();
  let currentUserId = currentUserProfile?.ownerId;
  if (currentUserId === ANONYMOUS_PRINCIPAL_ID.toString()) {
    currentUserId = undefined;
  }
  return <>
      <p>
        If you are logged in, your avatar or card will appear. If you are not
        logged in, enter an ownerId (e.g. 273960) or alias (e.g. brucehoff)
        below.
      </p>
      <UserCard ownerId={args.ownerId ?? currentUserId} {...args} />
    </>;
};
